@use "variables";
/* Start:: background */
.color-container {
  width: 3rem;
  height: 3rem;
  border-radius: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  line-height: 3rem;
}
.text-container {
  padding: 0.25rem 0.5rem;
  border-radius: variables.$default-radius;
  box-shadow: variables.$box-shadow;
}
/* Start::background color */
.bg-body {
    background-color: variables.$default-body-color !important;
}
/* End::background color */

/* Start::background color */
