@use "variables";
/* Start:: alerts */
.alert {
    padding: 0.625rem 0.85rem;
    border-radius: variables.$default-radius;
    font-size: 0.8125rem;
    &.alert-dismissible {
        padding: 0.625rem 2.25rem 0.625rem 0.85rem;
    }
    .btn-close {
        background-image: none;
        padding: 0.95rem;
        display: flex;
        align-items: center;
        justify-content: center;
        i {
            font-size: 1.5rem;
            line-height: 1.5rem;
        }
    }
    &:last-child {
        margin-bottom: 0;
    }
    .alert-link {
        font-weight: 600;
    }
}
[dir="rtl"] {
    .alert.alert-dismissible {
        padding: 0.625rem 0.85rem 0.625rem 2.25rem;
    }
}

/* Basic Alerts */
@mixin custom-close($color) {
    background-color: $color;
    color: variables.$white;
    opacity: 1;
    border-radius: 50px;
    box-shadow: variables.$box-shadow;
    padding: 0.85rem;
}
.alert-warning {
    background-color: rgba(var(--warning-rgb),0.1);
    color: variables.$warning;
    border-color:  rgba(var(--warning-rgb),0.1);
    .alert-link {
        color: variables.$warning;
    }
    .btn-close {
        color: variables.$warning;
        &.custom-close {
            @include custom-close(variables.$warning);
        }
    }
}
.alert-primary {
    background-color: variables.$primary-01;
    color: variables.$primary;
    border-color:  variables.$primary-01;
    .alert-link {
